*{
    margin: 0;
    padding: 0;
}
main{
    position: relative;
    top: 12vh;
    width: 100%;
    height: 100%;
}

article#ArtCprincipal{
    position: relative;
    display: flex;
    flex-direction: column;

    min-width: 20vh;
    width: 80%;
    height: auto;
    
    top: 2vh;
    bottom: 4em;
    margin: auto;
    
    padding: 1.4em;
    z-index: 2;

    border-radius: 2em;
}
/*pc*/
@media only screen and (min-width: 667px){
    article#ArtCprincipal{
        width: 90%;
    }
}
article#ArtCprincipal h1{
    margin: auto;
    font-size: 12vh;

    height: 18vh;
}

div#relleno{
    position: relative;
    width: 100%;
    height: 5vh;
}

