.fhead{
    position: fixed;
    top: 12vh;
    overflow: hidden;
    z-index: 10;
}
.fhead>ul{
    overflow: hidden;
}
.fhead>ul>li{
    list-style: none;
}

nav#focusTools[fc='unfocus']{
    width: 100%;
    height: 0;
}
nav#focusIndex[fc='unfocus']{
    width: 0;
    height: 100%;
}

div.SelTypePlant{
    position: absolute;
    width: 0;
    height: auto;
    overflow: hidden;
}
div.SelTypePlant[fc='Yesfc']{
    position: relative;
    width: auto;
}
div.SelTypePlant[fc='Yesfc']>button{
    width: 100%;
    font-size: 3vh;
}

/*
****INDEX
*/

nav#focusIndex{
    width: 40vh;
    height: 100%;
    background-color: rgb(214, 46, 46);
    flex-direction: column;
    justify-content: center;
    z-index: 11;
}
nav#focusIndex>div{
    height: 1em;
}

nav#focusIndex>ul{
    display: flex;
    width: 100%;
    height: auto;

    flex-direction: column;
    align-items: center;
}
nav#focusIndex>ul>li{
    position: relative;
    display: flex;
    width: 100%;
    height: 8vh;
    margin: .4vh;
    
    box-shadow: rgba(111, 21, 21, 0.2) 1px 1px 6px 2px;
}
nav#focusIndex>ul>li>button{
    position: relative;

    width: 90%;
    height: 90%;


    margin: auto;
    font-size: 3vh;
    justify-items: center;
    background-color: transparent;
    border: none;
}
nav#focusIndex li.Plants{
    position: relative;
    display: flex;

    
    flex-direction: column;
    align-items: center;
    height: auto;
}
nav#focusIndex li.Plants>button{
    position: relative;
    margin: auto;
    width: 100%;
    height: 8vh;
}

/*
****Tools
*/

nav#focusTools{
    display: flex;
    width: 100%;
    height: 40vh;
    background-color: rgb(87, 33, 137);
}

nav#focusTools>ul{
    position: relative;
    display: flex;
    
    padding: 1vh;
    margin: .8em;
    min-width: 25%;
    outline: dashed black;
    flex-grow: 1;
}

ul#CheckCor{
    width: 40%;
}

ul#CheckCor>form{
    position: relative;
    display: flex;

    width: 100%;
    height: 100%;
    flex-direction: column;
}


ul#CheckCor>form>li{
    position: relative;
    display: flex;
    flex-direction: row;

    width: 100%;
    flex-grow: 1;
}
ul#CheckCor>form>li>label{
    margin: auto;
    font-size: 1.4em;
    min-width: 70%;
    flex-grow: 1;
}
ul#CheckCor>form>li>input{
    margin: auto;
    width: 60%;
    height: 60%;
}
/*pc*/
@media only screen and (min-width: 667px){
    ul#CheckCor{
        max-width: 25%;
    }

    ul#CheckCor>form>li{
        max-height: 25%;
    }

    ul#CheckCor>form>li>label{
        font-size: 2em;
    }
}